/* Header.css */
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: white;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
  }
  
  .logo {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .nav {
    display: flex; /* O menu estará visível por padrão em telas grandes */
    gap: 20px;
  }
  
  .nav-btn {
    padding: 10px 20px;
    background-color: #444;
    border: none;
    color: white;
    font-size: 1rem;
    border-radius: 25px; /* Bordas arredondadas */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: all 0.3s;
  }
  
  .nav-btn:hover {
    background-color: #555;
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2);
  }
  
  .burger {
    display: none;
    background: none;
    border: none;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
  }
  
  .burger-bar {
    width: 30px;
    height: 4px;
    background-color: white;
    border-radius: 10px;
  }
  
  /* Menu no estilo hambúrguer para telas pequenas */
  @media (max-width: 768px) {
    .nav {
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: 50px;
      left: 0;
      width: 100%;
      background-color: #333;
      padding: 20px;
      gap: 10px;
      display: none; /* Escondido por padrão em telas pequenas */
    }
  
    .burger {
      display: flex; /* Exibe o ícone de hambúrguer em telas pequenas */
    }
  
    /* Quando o menu estiver aberto (quando isMenuOpen é true) */
    .nav.open {
      display: flex; /* Torna a navegação visível */
    }
  }
  